import React, {CSSProperties} from 'react';
import {
    AppBar,
    IconButton,
    Typography,
    Button,
    Toolbar,
    Grid,
    Drawer, Avatar,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu'
import {ViewModel} from "../application/ViewModel";
import Layout from "../application/Layout";
import MobileDetect from "../application/MobileDetect";
import avatar1 from "../assets/img/1.jpg";
import AppMenu from './AppMenu';

interface AppViewModel {
    open:boolean;
}

export default class App extends ViewModel<AppViewModel> {
    
    constructor(props:any) {
        super(props, {initial: {open: false}});
    }

    toggleDrawer(){
        this.payload = {...this.payload, open: !this.payload.open};
    }

    css = {
        root: {
            ...Layout.gridFullSize,
            gridTemplateRows: 'auto 1fr',
            justifyItems: 'center',
            overflow: MobileDetect.Any() ? 'hidden' : 'hidden auto'
        } as CSSProperties,
        grid: {
            width: MobileDetect.Any() ? '100%' : '50%',
            display: 'grid',
            padding: Layout.box(2).horizontal,
            paddingTop: Layout.box(2).top,
            overflow: this.isMobile ? 'hidden auto' : 'visible',
        } as CSSProperties,
        drawer:{
            width: '250px'
        }
    };

    notify(): React.ReactNode {
        return (
            <div style={this.css.root}>
                <AppBar position='static' elevation={0}>
                    <Toolbar>
                        <IconButton edge='start' color='inherit' aria-label='menu' onClick={()=> this.toggleDrawer() }>
                            <MenuIcon/>
                        </IconButton>
                        <Avatar src={process.env.PUBLIC_URL +'/logo192.png'}>OP</Avatar>
                        <Typography variant='h6'>
                            GENIUS
                        </Typography>
                    </Toolbar>
                </AppBar>
                <Grid style={this.css.grid} container>
                    {this.props.children}
                    <Drawer anchor={'left'} open={this.payload.open} onClose={()=>this.toggleDrawer()} >
                        <div style={this.css.drawer}>
                            <AppMenu onSelect={() => this.toggleDrawer()}/>
                        </div>
                    </Drawer>                    
                </Grid>
            </div>
        );
    }
}

