import {CSSProperties} from 'react';

export default class Layout {
    
    static get padding(): { horizontal: string; top: number; left: number; bottom: number; vertical: string; right: number } {
        return this._padding;
    }
    
    
    static get margin(): { horizontal: string; top: number; left: number; bottom: number; vertical: string; right: number } {
        return this._margin;
    }

    private static BASE_SIZE = 8;
    
    static gridFullSize ={
        display: 'grid',
        height: '100vh',
        width: '100%',
    } as CSSProperties

    
    static box = (scale: number = 1) => {
        return {
            bottom: Layout.BASE_SIZE * scale,
            left: Layout.BASE_SIZE * scale,
            right: Layout.BASE_SIZE * scale,
            top: Layout.BASE_SIZE * scale,
            horizontal: `0 ${Layout.BASE_SIZE * scale}px`,
            vertical: `${Layout.BASE_SIZE * scale}px 0`
        }
    }

    private static _margin = Layout.box();
    private static _padding = Layout.box();
}