import {ViewModel} from "../application/ViewModel";
import {List, ListItem, ListItemIcon, ListItemText} from "@material-ui/core";
import React from "react";
import HomeIcon from '@material-ui/icons/Home';

export default class AppMenu extends ViewModel<any> {

    onClick(event: () => void) {
        this.props.onSelect();
        event();
    }


    notify() {
        return <List>
            <ListItem button onClick={() => this.onClick(() => this.redirectTo('/admin'))}>
                <ListItemIcon>
                    <HomeIcon color='primary'/>
                </ListItemIcon>
                <ListItemText primary='Home'/>
            </ListItem>
        </List>
    }

}