import React, {Component, CSSProperties} from 'react';
import {
    Button,
    Divider, Grid,
    Typography
} from '@material-ui/core';
import Layout from '../Layout';


interface TitleProperties {
    title: string,
    subtitle?: string;
    color?: "initial" | "inherit" | "primary" | "secondary" | "textPrimary" | "textSecondary" | "error";
}

export default class Title extends Component<TitleProperties, any> {

    css = {
        header: {
            margin: Layout.box(4).vertical
        } as CSSProperties
    }

    render() {
        return (
            <Grid style={this.css.header} container direction={'column'}>
                <Grid container direction={'row'}>
                    <Grid item xs={8} container alignItems={'flex-start'} direction='column'>
                        <Typography variant={'h6'} color={this.props.color ?? 'textPrimary'} component={'p'}>
                            {this.props.title?.toUpperCase() ?? ''}
                        </Typography>
                        {
                            this.props.subtitle !== undefined
                                ? <Typography variant={'caption'} color={'textSecondary'}>
                                    {this.props.subtitle}
                                </Typography>
                                : undefined
                        }
                    </Grid>
                    <Grid item container xs={4} alignItems={'center'} justify={'flex-end'}>
                        {
                            this.props.children
                        }
                    </Grid>
                </Grid>
                <Divider/>
            </Grid>
        );

    }
}